import React from 'react';
import "./OrderStatus.css";
import {TYPE_DANGER, TYPE_DEFAULT} from "../../_types";
import ORDER_STATUSES from "../../_types/ORDER-STATUSES";
import Label from "arui-feather/label";

const colors = {
  [TYPE_DANGER] : '#ff1a1a',
  [TYPE_DEFAULT]: '#758289',
};

function OrderStatus({status: statusCode, className = ''}) {
  
  const status = ORDER_STATUSES[statusCode];
  
  if (!status) {
    return <div className="d-inline-block">Статус не описан</div>;
  }
  
  
  
  return (
    <div className={`d-inline-block order-status ${className}`} style={{backgroundColor: colors[status.type]}}>
      <Label size="s" isNoWrap theme="alfa-on-color">{status.message}</Label>
    </div>
  )
}

export default OrderStatus;
