import store from '../_store';
import {pushConfirm, pushNotification, removeConfirm} from "../_store/notices";

const noticeService = {
  pushNotification(title = '', message = '', options = {}) {
    store.dispatch(pushNotification({message, options: {...options, title}}));
  },
  
  pushConfirm(text, header, options={visible: true, isProcessing: false}) {
    return new Promise((resolve, reject) => {
      store.dispatch(pushConfirm({text, header, ...options,
        rejection: (id) => {
          reject('Cancel');
          noticeService.closeConfirm(id);
  
        },
        resolving: (id) => {
          resolve('Confirm');
          noticeService.closeConfirm(id);
        }
      }))
    });
  },
  
  closeConfirm(id) {
    store.dispatch(removeConfirm(id));
  }
};

export default noticeService;
