import React from 'react';
import './CompanyItem.css';
import Label from "arui-feather/label";
import TrashIcon from 'arui-feather/icon/action/delete';
import moment from "moment";
import OrderStatus from "./OrderStatus";
import IconButton from "arui-feather/icon-button";
import noticeService from "../../_services/notice.service";

function CompanyItem({company = {}, className = ''}) {
  
  return (
    <div className={"company-item d-block d-sm-flex scalable-hover " + className}>
      <div className="company-info width-1 d-flex flex-column mb-2">
        <Label size="l" className="mb-1">{company.name}</Label>
        <Label size="s" className="opacity-middle">{company.code}</Label>
      </div>
      <div className="company-comment width-1 mx-sm-4 mb-2">
        <Label size="s" className="opacity-high">
          {company.comment}
        </Label>
      </div>
      <div className="d-flex">
        <div className="company-status d-flex flex-column mr-3">
          <OrderStatus status={company.status} className="mb-2"/>
          <Label size="s" className="opacity-middle">
            Создано {moment(company.createdAt).format('DD.MM.YYYY')}
          </Label>
        </div>
        <div className="company-action ml-auto">
          <div className="button-accent">
            <IconButton onClick={handleRemoveButton}><TrashIcon/></IconButton>
          </div>
        </div>
      </div>
    </div>
  );
  
  function handleRemoveButton() {
    noticeService.pushConfirm(<Label size="l">Вы дейстивтельно хотите удалить сервис?</Label>).then(() => {
      console.log('Yes')
    }).catch(() => {
      console.log('No');
    })
  }
}

export default CompanyItem;
